.header {
    width: 100vw;
    height: 60px;
    top: 0;
    position: fixed;
    display: flex;
    align-items: center;
    background-color: rgb(10, 10, 10);
    z-index: 1;
  }
  
  .logo {
    font-size: 1.75rem;
    color: rgb(200, 200, 200);
    font-weight: bold;
    justify-content: left;
  }
  
  .header ul {
    list-style: none;
    margin: 10rem;
    padding: 10rem;
    display: flex;
    align-items: baseline;
  }
  
  .header li {
    margin-left: 3rem;
  }
  
  .header a {
    text-decoration: none;
    font-size: 1.25rem;
    color: rgb(150, 150, 150);
  }
  
  .header a:hover,
  .header a:active,
  .header a.active {
    color: white;
  }